@import '../App.css';
.contact .contacts {
    width: fit-content;
}
.contact
.left {
    padding-top: 5%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.right
.container {
    width: auto;
    flex-direction: column;
    gap: 2rem;
    overflow: hidden;
}
.contact
.left p {
    inline-size: 50%;
}
.contact 
.contacts ul {
    flex-direction: row;
}
.contact
.contacts li {
    transition: 0.2s all;
    padding: 2.5%;
}
.contact
.contacts li:hover {
    background-color: darkorange;
    transform: translateY(2.5px);
}
.contact 
.right 
.container {
    background-color: var(--darker-green);
    border-radius: 10px;
}
.contact 
.right h2 {
    color: white;
}
form {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, auto);
    column-gap: 0.5rem;
    row-gap: 0.75rem;
    grid-template-rows: repeat(2, 1fr);
    
}
.wrap {
    display: grid;
    grid-template-columns: (2, 1fr);
    height: fit-content;
}
.w-100 {
    grid-column: span 2;
    height: fit-content;
}
.right .button {
    transition: 0.2s all;
}
.right .button:hover {
    background-color: darkorange;
    color: black;
}
label,
input {
    color: white;
    font-weight: 500;
    height: fit-content;
    padding: 1%;
}
input, textarea {
    padding: 5%;
    border-radius: 5px;
    background-color: #222;
    border: none;
    color: white;
}
textarea {
    height: 150px;
    resize: vertical;
    min-height: 80px;
    max-height: 235px;
}
.button:disabled, .button:disabled:hover {
    background-color: lightcoral;
}
.bottom {
    display: inherit;
    width: 100%;
}
@media all and (max-width: 915px) {
    .contact {
        height: auto;
    }
    .contact
    .container {
        flex-direction: column;
        gap: 2rem;
    }
    .contact
    .left,
    .contact
    .right {
        width: 100%;
    }
    .contact
    .left {
        text-align: center;
        align-items: center;
    }
}
@media all and (max-width: 430px) {
    .contact
    .container {
        padding: 5%;
    }
    .contact
    .right
    .container {
        padding: 3.5%;
    }
}
@media all and (min-width: 1080px) {
    textarea {
        max-height: 150px;
    }
}