:root {
  --light-green: #cad2c5;
  --medium-green: #84a98c;
  --olive-green: #52796f;
  --dark-green: #354f52;
  --darker-green: #2f3e46;

  --p-font: 1.5rem;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
section {
  height: 100vh;
}
html {
  scroll-behavior: smooth;
}
.olive-green {
  color: var(--olive-green);
}
.darker-green {
  color: var(--darker-green);
}
.dark-green {
  color: var(--dark-green);
}
.asu {
  color: #8C1D40;
  font-weight: 600;
}
.container {
  height: 100%;
  display: flex;
  padding: 10%;
  margin: 0 auto;
}
.col {
  width: 50%;
}
.footer {
  text-align: center;
  font-weight: 800;
  padding: 1%;
}
body {
  padding-top: 60px;
}