@import "../App.css";
header {
    padding: 1.5rem;
    margin: 0 auto;
    top: 0;
    left: 0;
    position:fixed;
    width: 100%;
    background-color: #fff;
    z-index: 9999;
}
.header-content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-right: 0.5rem;
    z-index: 99;
}
.nav-links {
    display: flex;
    gap: 1rem;
}
.nav-link {
    font-weight: 500;
    transition: 0.3s all;
    padding: 0.5rem;
}
.nav-link:hover,
.nav-link.active {
    border-radius: 3px;
    color: var(--darker-green);
    -webkit-box-shadow: 0px 2.5px 0px 0px var(--darker-green);
    -moz-box-shadow: 0px 2.5px 0px 0px var(--darker-green);
    box-shadow: 0px 2.5px 0px 0px var(--darker-green);
}
.hamburger {
    width: fit-content;
    display: none;
    height: fit-content;
    cursor: pointer;
    padding: 3px 8px;
    border-radius: 5px;
    transition: 0.2s;
    z-index: 9999;
    justify-content: flex-end;
}
.hamburger:hover {
    background: #fff;
}
.hamburger div {
    width: 30px;
    height: 2px;
    margin: 6px 0;
    background: var(--darker-green);
}
@media all and (max-width: 646px) {
    nav {
        position: absolute;
        right: -300px;
        top: 0;
        width: 280px;
        height: 100vh;
        background-color: #fff;
        transition: 0.2s;
        flex-direction: column;
        padding: 5%;
        justify-content: center;
    }
    .show-menu {
        right: 0;
    }
    nav ul {
        flex-direction: column;
        padding: 0 20px;
        margin-top: 30px;
        justify-content: center;
        height: 100%;
        gap: 10%;
    }
    nav ul li a {
        font-size: 2em;
        margin-bottom: 5px;
        padding: 10px 15px;
        border-radius: 5px;
    }
    .hamburger {
        display: block;
    }
}